<template>
	<view class="body">
		<!-- 步骤条组件 -->
		<u-steps :current="steps" activeColor="#ffd656" inactiveColor="#cfcfcf" :dot="true">
			<u-steps-item></u-steps-item>
			<u-steps-item></u-steps-item>
			<u-steps-item></u-steps-item>
		</u-steps>
		<!-- 步骤条文字 -->
		<view class="textList">
			<view :class="{checked:steps==0 || steps==1 || steps==2}">
				<view class="text">Step1</view>
				<view class="text">确认信息</view>
			</view>
			<view :class="{checked:steps==1 || steps==2}">
				<view class="text">Step2</view>
				<view class="text">估分</view>
			</view>
			<view :class="{checked:steps==2}">
				<view class="text">Step3</view>
				<view class="text">查看报告</view>
			</view>
		</view>
		<view class="content">
			<view class="list">
				<view class="cell">
					<view class="label">省市:</view>
					<view class="check">{{data.area_name}}</view>
				</view>
				<view class="cell">
					<view class="label">招生单位:</view>
					<view class="check">{{data.school_name}}</view>
				</view>
				<view class="cell">
					<view class="label">专业名称:</view>
					<view class="check">{{data.three_major_name}}</view>
				</view>
			</view>
			<view class="title">为了尽量保证排名的准确性每个人仅有<view>1次</view>生成报告的机会，请认真估分哦~</view>
			<view class="subjectList">
				<view class="cell" v-if="data.one_subject_attr!=3">
					<view class="cellLeft">
						<view class="name">{{data.one_subject_title}}</view>
						<view class="num">分数：{{data.one_subject_score || '未估分'}}</view>
					</view>
					<view class="cellRight">
						<view class="text" v-if="data.one_subject_attr==1" @click="toAssessment(1,data.one_subject_id,data.one_subject_state)"><view v-if="data.one_subject_state==1">重新</view><view v-else>做题</view>估分</view>
						<view class="text" @click="toAssessment(2,data.one_subject_id,data.one_subject_state)">自主估分</view>
					</view>
				</view>
				<view class="cell" v-if="data.two_subject_attr!=3">
					<view class="cellLeft">
						<view class="name">{{data.two_subject_title}}</view>
						<view class="num">分数：{{data.two_subject_score || '未估分'}}</view>
					</view>
					<view class="cellRight">
						<view class="text" v-if="data.two_subject_attr==1" @click="toAssessment(1,data.two_subject_id,data.two_subject_state)"><view v-if="data.two_subject_state==1">重新</view><view v-else>做题</view>估分</view>
						<view class="text" @click="toAssessment(2,data.two_subject_id,data.two_subject_state)">自主估分</view>
					</view>
				</view>
				<view class="cell" v-if="data.three_subject_attr!=3">
					<view class="cellLeft">
						<view class="name">{{data.three_subject_title}}</view>
						<view class="num">分数：{{data.three_subject_score || '未估分'}}</view>
					</view>
					<view class="cellRight">
						<view class="text" v-if="data.three_subject_attr==1" @click="toAssessment(1,data.three_subject_id,data.three_subject_state)"><view v-if="data.three_subject_state==1">重新</view><view v-else>做题</view>估分</view>
						<view class="text" @click="toAssessment(2,data.three_subject_id,data.three_subject_state)">自主估分</view>
					</view>
				</view>
				<view class="cell" v-if="data.four_subject_attr!=3">
					<view class="cellLeft">
						<view class="name">{{data.four_subject_title}}</view>
						<view class="num">分数：{{data.four_subject_score || '未估分'}}</view>
					</view>
					<view class="cellRight">
						<view class="text" v-if="data.four_subject_attr==1" @click="toAssessment(1,data.four_subject_id,data.four_subject_state)"><view v-if="data.four_subject_state==1">重新</view><view v-else>做题</view>估分</view>
						<view class="text" @click="toAssessment(2,data.four_subject_id,data.four_subject_state)">自主估分</view>
					</view>
				</view>
			</view>
			<view class="reportButtons">
				<button class="beginButton" @click="backAssessment">重置信息</button>
				<button class="beginButton" @click="submit">生成报告</button>
			</view>
		</view>
		<view class="module" v-if="show">
			<view class="model">
				<view class="title">专业课成绩</view>
				<view class="text">请结合预估，给你的专业课打分</view>
				<view class="scorer">
					<view class="scorerLeft" @click.stop="reduceValue">-</view>
					<input type="number" v-model="value" @focus="focusScore">
					<view class="scorerRight" @click.stop="increaseValue">+</view>
				</view>
				<view class="sure" @click.stop="setNum">确定</view>
			</view>
		</view>
		<view class="module" v-else-if="showSure">
			<view class="models">
				<view class="title">确认生成报告吗？</view>
				<view class="text">生成报告页之后无法再次修改估分，请确认你的估分哦！</view>
				<view class="buttons">
					<view class="cancel" @click="showSure=false">取消</view>
					<view class="sure" @click="sureReport">确定</view>
				</view>
			</view>
		</view>
		<view class="module" v-else-if="showReset">
			<view class="reset">
				<view class="title">确认返回重填信息吗？</view>
				<view class="buttons">
					<view class="cancel" @click="showReset=false">取消</view>
					<view class="sure" @click="sureResubject">确定</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		assessment, reQuestion, paper, selfAssessment, reSubject
	} from '@/common/http.api.js';
	export default {
		data() {
			return {
				token:'',
				steps: 1, //当前步骤
				value:0, //学生预估成绩
				show:false, //显示打分器
				showSure:false, //显示确认窗
				showReset:false, //显示重新估分确认框
				subject_id:'',
				subjectList:[],
				data:{}
			}
		},
		onLoad() {
			this.token = store.state.vuex_token
		},
		onShow() {
			assessment({token:this.token}).then(res=>{
				console.log(res)
				this.data = res.data.data
				if(res.data.data.state==1){
					uni.navigateTo({
						url:'/pages/assessment/report'
					})
				}
				if(res.data.data.one_subject_title!='无'){
					this.subjectList.push({name:res.data.data.one_subject_title, status:res.data.data.one_subject_score})
				}
				if(res.data.data.two_subject_title!='无'){
					this.subjectList.push({name:res.data.data.two_subject_title, status:res.data.data.two_subject_score})
				}
				if(res.data.data.three_subject_title!='无'){
					this.subjectList.push({name:res.data.data.three_subject_title, status:res.data.data.three_subject_score})
				}
				if(res.data.data.four_subject_title!='无'){
					this.subjectList.push({name:res.data.data.four_subject_title, status:res.data.data.four_subject_score})
				}
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 生成报告
			submit() {
				let isNotReady = false;
				this.subjectList.map(item=>{
					if(item.status!=null){
						console.log(item.status)
					}else{
						isNotReady = true
					}
				})
				console.log('this.subjectList:',this.subjectList)
				if(isNotReady==true){
					this.$u.toast("请先完成所有科目估分")
				}else{
					console.log("完成")
					this.showSure = true
				}
			},
			// 显示确认框
			backAssessment() {
				this.showReset = true 
			},
			// 重新估分
			sureResubject() {
				reSubject({token:this.token}).then(res=>{
					console.log(res)
					this.$u.toast(res.data.data+',即将返回')
					setTimeout(()=>{
						this.show = false 
						uni.switchTab({
							url:'/pages/assessment/assessment'
						})
					},1500)
				}).catch(err=>{
					console.log(err)
				})
			},
			// 打分器估分
			setNum(){
				this.subjectList = []
				let notNumber = isNaN(this.value)
				// strings.replace(/(^s*)|(s*$)/g, "")
				if(notNumber==true){
					this.$u.toast("请输入数字")
					this.value = ''
					console.log('this.value:',this.value)
				}else{
					if(this.value<=0){
						this.$u.toast("估分不小于1")
						this.value = ''
					}else{
						selfAssessment({token:this.token, score:this.value, subject_id:this.subject_id}).then(res=>{
							console.log(res)
							this.$u.toast(res.data.data)
							this.show = false
							this.value = 0
							assessment({token:this.token}).then(res=>{
								console.log(res)
								this.data = res.data.data
								if(res.data.data.one_subject_title!='无'){
									this.subjectList.push({name:res.data.data.one_subject_title, status:res.data.data.one_subject_score})
								}
								if(res.data.data.two_subject_title!='无'){
									this.subjectList.push({name:res.data.data.two_subject_title, status:res.data.data.two_subject_score})
								}
								if(res.data.data.three_subject_title!='无'){
									this.subjectList.push({name:res.data.data.three_subject_title, status:res.data.data.three_subject_score})
								}
								if(res.data.data.four_subject_title!='无'){
									this.subjectList.push({name:res.data.data.four_subject_title, status:res.data.data.four_subject_score})
								}
							}).catch(err=>{
								console.log(err)
							})
						}).catch(err=>{
							console.log(err)
						})
					}
				}
				
			},
			// 前往报告
			sureReport() {
				this.showSure = false
				if(this.$u.utils.networkType()==true){
					paper({token:this.token}).then(res=>{
						console.log(res)
						this.$u.toast(res.data.data)
						if(res.data.data=='提交成功'){
							uni.redirectTo({
								url:'/pages/assessment/report'
							})
						}
					}).catch(err=>{
						console.log(err)
					})
				}else{
					this.$u.toast('当前网络状况不佳~')
				}
			},
			// 预估分减一
			reduceValue() {
				if(this.value>0){
					this.value = Number(this.value)
					this.value-=1
				}
			},
			// 预估分加一
			increaseValue() {
				if(this.value<100){
					this.value = Number(this.value)
					this.value+=1
				}
			},
			// 自主打分聚焦
			focusScore() {
				this.value = ''
			},
			// 开始估分
			toAssessment(item,id,status) {
				if(item==1){
					if(status!=0){
						reQuestion({token:this.token, subject_id:id}).then(res=>{
							console.log(res)
							this.$u.toast(res.data.data)
							assessment({token:this.token}).then(res=>{
								console.log(res)
								this.data = res.data.data
							}).catch(err=>{
								console.log(err)
							})
						}).catch(err=>{
							console.log(err)
						})
					}else{
						uni.navigateTo({
							url:'/pages/assessment/answer'
						})
						uni.setStorage({
							key: 'subject_id',
							data: id,
							success: function () {
								console.log('success');
							}
						});
						uni.setStorage({
							key: 'isAnswerCard',
							data: 0,
							success: function () {
								console.log('success');
							}
						});
					}
				}else{
					this.subject_id = id
					this.show = true
				}
			}
		}
	}
</script>

<style lang="scss">
	.body {
		min-height: 100vh;
		background-color: #f7f6fb;
		padding: 30rpx;
		padding-bottom: 0;

		.u-steps-item__wrapper {
			width: 0 !important;
		}

		.u-steps-item__wrapper__dot {
			width: 20rpx !important;
			height: 20rpx !important;
		}

		.textList {
			display: flex;
			width: 80%;
			font-size: 26rpx;
			justify-content: space-between;
			margin: 0 auto;
			color: #606266;

			.text {
				line-height: 42rpx;
				text-align: center;
			}

			.checked {
				color: #ffd656;
			}
		}

		.content {
			border-radius: 4rpx;
			background-color: #fff;
			margin: 20rpx 0;
			padding: 30rpx;
			.reportButtons{
				display: flex;
				justify-content: space-between;
			}
			.list {
				.cell {
					display: flex;
					justify-content: space-between;
					align-items: center;
					font-size: 28rpx;
					line-height: 58rpx;
					margin-bottom: 10rpx;
					color: #545454;

					.label {
						width: 30%;
					}

					.check {
						width: 60%;
						border: 1rpx solid #eee;
						padding: 0 20rpx;
						.uni-select {
							font-size: 28rpx !important;
							border-radius: 0 !important;
							line-height: 68rpx;
						}
					}
				}

				.disable {
					.uni-select__input-text {
						color: #b2b2b2 !important;
					}
				}
			}

			.title {
				margin: 30rpx 0;
				text-align: center;
				line-height: 58rpx;
				font-size: 30rpx;
				font-weight: 600;
				view{
					color: #fe4910;
					display: inline-block;
				}
			}
			.subjectList{
				margin: 30rpx 0;
				.cell{
					border-radius: 20rppx;
					background-color: #f7f6fb;
					display: flex;
					padding: 30rpx;
					font-size: 28rpx;
					justify-content: space-between;
					align-items: center;
					margin-bottom: 20rpx;
					.cellLeft{
						.num{
							font-size: 24rpx;
							margin-top: 10rpx;
							color: #747474;
						}
					}
					.cellRight{
						display: flex;
						.text{
							border: 1rpx solid #ffd656;
							width: 150rpx;
							text-align: center;
							line-height: 58rpx;
							border-radius: 4rpx;
							background-color: #fff;
							color: #fed754;
							margin-right: 10rpx;
							view{
								display: inline-block;
							}
						}
						view:last-child{
							margin-right: 0;
						}
					}
				}
			}
			.testMoudel {
				.part {
					display: flex;
					justify-content: space-between;

					.cell {
						width: 40%;

						.subNum {
							font-size: 34rpx;
						}

						.check {
							background-image: url(../../static/assessment/sub_icon.png);
							background-repeat: no-repeat;
							background-position: 90% center;
							background-size: 20rpx;

							.uni-select {
								text-align: left;
								font-size: 28rpx !important;
								border: none !important;
								line-height: 88rpx;
								padding-left: 0 !important;
							}
						}

						.defult {
							.uni-select__input-text {
								color: #b2b2b2;
							}
						}
					}
				}

				.part:first-child {
					border-bottom: 1rpx solid #eee;
					margin-bottom: 30rpx;
				}
			}

			.beginButton {
				background-color: #ffd656;
				color: #fff;
				font-size: 38rpx;
				width: 300rpx;
				margin: 50rpx auto;
				border-radius: 50rpx;
				text-align: center;
				padding: 0;
				line-height: 78rpx;
			}
		}
		.module{
			width: 100vw;
			height: 100vh;
			position: fixed;
			top: 0;
			right: 0;
			z-index: 999;
			background: rgba(0, 0, 0, .3);
			display: flex;
			justify-content: center;
			align-items: center;
			.model{
				background-color: #fff;
				border-radius: 20rpx;
				width: 85%;
				padding: 40rpx 20rpx;
				.title{
					font-size: 34rpx;
					font-weight: 600;
					text-align: center;
					letter-spacing: 2rpx;
					margin-bottom: 30rpx;
				}
				.text{
					font-size: 30rpx;
					letter-spacing: 2rpx;
					text-align: center;
					color: #a3a3a3;
				}
				.scorer{
					display: flex;
					width: 70%;
					margin: 30rpx auto;
					text-align: center;
					font-size: 38rpx;
					border-radius: 50rpx;
					line-height: 100rpx;
					background-color: #fff;
					border: 1rpx solid #ececec;
					overflow: hidden;
					.scorerLeft{
						width: 150rpx;
						background-color: #f3f3f3;
						font-size: 54rpx;
						color: #d8d8d8;
					}
					input{
						height: 100rpx;
						line-height: 100rpx;
					}
					.scorerRight{
						width: 150rpx;
						background-color: #ffe89c;
						font-size: 54rpx;
						color: #ffd656;
					}
				}
				.sure{
					width: 280rpx;
					font-size: 30rpx;
					text-align: center;
					border-radius: 50rpx;
					margin: 0 auto;
					line-height: 78rpx;
					background-color: #ffd656;
					color: #fff;
				}
			}
			.models{
				background-color: #fff;
				border-radius: 20rpx;
				width: 70%;
				padding: 50rpx 40rpx;
				.title{
					font-size: 54rpx;
					font-weight: 700;
				}
				.text{
					font-size: 32rpx;
					line-height: 58rpx;
					margin: 40rpx 0;
				}
				.buttons{
					display: flex;
					justify-content: space-between;
					align-items: center;
					.cancel{
						background-color: #fff;
						border: 1rpx solid #ffd656;
						border-radius: 50rpx;
						color: #ffd656;
						line-height: 78rpx;
						font-size: 34rpx;
						width: 40%;
						text-align: center;
					}
					.sure{
						background-color: #ffd656;
						border: 1rpx solid #ffd656;
						border-radius: 50rpx;
						color: #fff;
						line-height: 78rpx;
						font-size: 34rpx;
						width: 40%;
						text-align: center;
					}
				}
			}
			
			.reset{
				background-color: #fff;
				border-radius: 20rpx;
				width: 56%;
				.title{
					font-size: 32rpx;
					font-weight: 500;
					text-align: center;
					padding: 40rpx 0;
					margin-bottom: 20rpx;
				}
				.buttons{
					width: 100%;
					display: flex;
					justify-content: space-between;
					align-items: center;
					view{
						line-height: 78rpx;
						font-size: 30rpx;
						width: 50%;
						text-align: center;
					}
					.cancel{
						background-color: #eee;
						border-bottom-left-radius: 20rpx;
						border: 1rpx solid #eee;
					}
					.sure{
						background-color: #ffd656;
						color: #fff;
						border-bottom-right-radius: 20rpx;
						border: 1rpx solid #ffd656;
					}
				}
			}
		}
	}
</style>
